<template>
    <yd-layout>
        <div v-show="formFlag">
            <div class="headerTime item" v-for="item in yearObj" v-bind:key="item.alyear">
                <yd-flexbox>
                    <div class="item_one">{{item.alyear}}总计剩余年假:</div>
                    <div class="item_two" v-text="item.lastyearall - item.lastyearthen + item.yearall - item.yearthen"></div>
                    <div class="item_one">天,其中沿用年假</div>
                    <div class="item_two" v-text="item.lastyearall - item.lastyearthen"></div>
                    <div class="item_one">天,核定年假</div>
                    <div class="item_two" v-text="item.yearall - item.yearthen"></div>
                    <div class="item_one">天</div>
                </yd-flexbox>
            </div>
            <div class="mainContentFluid part_one">
                <div class="fluidWidth">
                    <div class="advice">
                        <div class="commonAdvice">
                            请假类型
                        </div>
                        <div class="selectAdvice sort">
                            <yd-navbar-next-icon>{{confirmItem}}</yd-navbar-next-icon>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid part_two leave_two">
                <yd-cell-group>
                    <yd-cell-item arrow>
                        <span slot="left" class="normalSize">开始时间</span>
                        <yd-datetime :class="'dateColor'" type="date" v-model="startdate" slot="right"></yd-datetime>
                    </yd-cell-item>
                    <yd-cell-item arrow>
                        <span slot="left" class="normalSize">结束时间</span>
                        <yd-datetime :class="'dateColor'" type="date" v-model="enddate" slot="right"></yd-datetime>
                    </yd-cell-item>
                </yd-cell-group>
            </div>
            <div class="mainContentFluid groundTrip">
                <yd-cell-group class="cellGrop">
                    <yd-cell-item>
                        <span slot="left" :class="'bold'">时长(天)</span>
                        <yd-input slot="right" class="dateInput" v-model="inputNum"></yd-input>
                    </yd-cell-item>
                </yd-cell-group>
            </div>
            <div class="mainContentFluid part_five">
                <div class="fluidWidth">
                    <div class="advice">
                        <div class="commonAdvice">
                            工作代理人
                        </div>
                        <div class="selectAdvice sort" v-on:click="statusPopup = true;popupArr = agentList;">
                            <yd-navbar-next-icon v-if="confirmItem2 == ''">请选择</yd-navbar-next-icon>
                            <yd-navbar-next-icon v-if="confirmItem2 != ''">{{confirmItem2}}</yd-navbar-next-icon>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid partOne" v-if="userInfo.role==2||userInfo.orgname=='总经理室'">
                <div class="confirTop">
                    <div class="advice02">
                        <div class="confirmTrip">
                            出国（境）
                        </div>
                        <div class="selectAdvice02">
                            <span id="js_yesBtn" class="selectBtn rSpace" v-bind:class="{selectActive:isOut==1}" v-on:click="isOut=1">是</span>
                            <span id="js_noBtn" class="selectBtn" v-bind:class="{selectActive:isOut==0}" v-on:click="isOut=0">否</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid part_two leave_two" v-if="(userInfo.role==2||userInfo.orgname=='总经理室')&&isOut==1">
                <yd-cell-group>
                    <yd-cell-item arrow>
                        <span slot="left" class="normalSize">离境时间</span>
                        <yd-datetime :class="'dateColor'" :start-date="current" type="date" v-model="outDate" slot="right"></yd-datetime>
                    </yd-cell-item>
                    <yd-cell-item arrow>
                        <span slot="left" class="normalSize">入境时间</span>
                        <yd-datetime :class="'dateColor'" :start-date="current" type="date" v-model="enterDate" slot="right"></yd-datetime>
                    </yd-cell-item>
                </yd-cell-group>
            </div>
            <div class="mainContentFluid part_one" :class="{mainHistory:isOut==0}">
                <div class="fluidWidth">
                    <div class="advice history">
                        <div class="commonAdvice historyWord">
                            历史休假记录
                        </div>
                        <div class="selectAdvice sort">
                            <yd-navbar-next-icon v-on:click.native="toHistory()">查看</yd-navbar-next-icon>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid" v-if="userInfo.role==1||userInfo.role==2||userInfo.orgname=='总经理室'">
                <yd-cell-group class="cellGrop">
                    <yd-cell-item>
                        <span slot="left" :class="'bold'">休假地点</span>
                        <yd-input slot="right" v-if="userInfo.role==1&&userInfo.orgname!='总经理室'" class="dateInput" v-model="address"></yd-input>
                        <yd-input slot="right" v-if="userInfo.orgname=='总经理室'||userInfo.role==2" placeholder="请精确到国家和具体的城市" class="dateInput" v-model="address"></yd-input>
                    </yd-cell-item>
                </yd-cell-group>
            </div>
            <div class="mainContentFluid">
                <div class="adviceTxt">
                    <div class="commonRemark">请假事由</div>
                    <trsTextarea v-model="remark" placeholder="请填写您的请假事由"></trsTextarea>
                </div>
            </div>
            <div class="mainContentFluid" v-if="approvelog.length!=0">
                <div class="historyWidth">
                    <div class="approHistory">
                        <div class="title">
                            审批记录
                        </div>
                        <div v-for="(item, index) in approvelog" :key="item.partiname">
                            <yd-flexbox>
                                <div class="approName nameSize" v-if="item.activityDefName!='领导秘书预审'">{{item.partiname}}：</div>
                                <yd-flexbox-item class="neirongSzie" v-html="transferString(item.neirong)"></yd-flexbox-item>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="approTime">{{formatDate(item.submitDate, 'yyyy-MM-dd')}}</div>
                                <div class="approTime">{{formatDate(item.submitDate, 'hh:mm')}}</div>
                            </yd-flexbox>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid" v-show="workId==undefined && userInfo.role != 2">
                <div class="fluidWidth approveWidth">
                    <div class="approval">
                        审批人
                    </div>
                    <div class="name_circle">
                        <ul>
                            <li v-for="(item, index) in approver">
                                <div class="lastname" v-bind:class="{'check':checkNum==index}" v-on:click="checkNum == index ? checkNum=99 : checkNum=index">
                                    {{item.empname.substring(0,1)}}
                                    <div class="nameClick">
                                    </div>
                                    <div class="imgClick">
                                    </div>
                                </div>
                                <div class="name" v-text="item.empname">
                                </div>
                            </li>
                        </ul>
                        <div class="clear"></div>
                    </div>
                </div>
            </div>
        </div>
        <yd-tabbar slot="tabbar" v-show="formFlag && userInfo.role!=2 && status==0 && statusPopup==false && workId==undefined">
            <div class="footer">
                <yd-button v-on:click.native="userInfo.orgname=='总经理室'?sendOutManager():sendOut()" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">送领导审批</yd-button>
            </div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-show="formFlag && userInfo.role==2 && status==0  && statusPopup==false">
            <div class="footer">
                <yd-button v-on:click.native="sendAccepted()" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">提交</yd-button>
            </div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-show="formFlag && userInfo.role!=2 && statusPopup==false && status==0 && workId!=undefined">
            <div class="assistBottom">
                <div class="agentLeader">
                    <yd-flexbox>
                        <div class="examineLogo_txt01" v-on:click="toSelectLeader()">送领导审批</div>
                    </yd-flexbox>
                </div>
                <div class="agentLeader">
                    <yd-flexbox>
                        <div class="examineLogo_txt02" v-on:click="isCansend!=''?$dialog.toast({mes: isCansend,timeout: 2000}):cancelHoliday()">提交</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <yd-popup v-model="statusPopup" v-show="statusPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup = false;confirmItem2=agentItem;">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-for="(item,index) in popupArr" v-bind:class="{active:statusRadio==index}" v-on:click="statusRadio==index?statusRadio=99:statusRadio=index;statusRadio!=99?agentItem=item:agentItem='';" v-text='item'></li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'selected',
    data() {
        return {
            statusRadio: 99,
            statusPopup: false,
            popupArr: [],
            confirmItem: '年休假',
            confirmItem2: '',
            remark: '',
            address: '',
            checkNum: 99,
            approver: [],
            leaderIDs: '',
            startdate: '',
            enddate: '',
            // btnActivity: '',
            approveType: '',
            agentList: [],
            agentObj: [],
            daysNum: '',
            yearObj: [],
            inputNum: '',
            agentId: '',
            formInfo: {},
            agentItem: '',
            activityNum: '',
            myId: '',
            formFlag: true,
            approvelog: [], // 审批流程集合
            nextActivity: '',
            nextLeaderIDs: '',
            queryTypeArr: [23, 24, 25],
            appLyName: '', // 申请人的姓名
            status: this.$route.params.status,
            workId: this.$route.params.workId,
            outDate: '',
            enterDate: '',
            isOut: 1,
            current: '',
            isCansend: '',
            errorTime: 0
        }
    },
    mounted() {
        document.setTitle('请休假');
        let vm = this;
        vm.current = vm.formatDate(new Date(), 'yyyy-MM-dd hh:mm');
        vm.getNowFormatDate();
        vm.pageRender();
        if (vm.userInfo.role == 2) {
            vm.getAcceptor();
        } else {
            vm.getApprover();
        }
        vm.getVuex();
        vm.errorTime = 0;
        vm.$dialog.loading.open('加载中...');
        // if (vm.workId != undefined) {

        // }
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            holidayInfo: 'getHolidayInfo'
        })
    },
    watch: {
        startdate: function() {
            var vm = this;
            var start = Date.parse(vm.startdate);
            var end = Date.parse(vm.enddate);
            var msNum = end - start;
            vm.daysNum = msNum / 1000 / 60 / 60 / 24;
            vm.setHolidayInfo({
                startDate: vm.startdate
            });
        },
        enddate: function() {
            var vm = this;
            var start = Date.parse(vm.startdate);
            var end = Date.parse(vm.enddate);
            var msNum = end - start;
            vm.daysNum = msNum / 1000 / 60 / 60 / 24;
            vm.setHolidayInfo({
                endDate: vm.enddate
            });
        },
        inputNum: function() {
            var vm = this;
            if (isNaN(vm.inputNum)) {
                vm.$dialog.toast({
                    mes: '时长请输入数字',
                    timeout: 2000
                });
            }
            vm.setHolidayInfo({
                days: vm.inputNum
            });
        },
        address: function() {
            let vm = this;
            vm.setHolidayInfo({
                address: vm.address
            });
        },
        remark: function() {
            let vm = this;
            vm.setHolidayInfo({
                remark: vm.remark
            });
        },
        checkNum: function() {
            let vm = this;
            vm.setHolidayInfo({
                checkNum: vm.checkNum
            });
        },
        approver: function() {
            let vm = this;
            vm.setHolidayInfo({
                approver: vm.approver
            });
        },
        agentObj: function() {
            let vm = this;
            vm.setHolidayInfo({
                agentObj: vm.agentObj
            });
        },
        statusRadio: function() {
            let vm = this;
            vm.setHolidayInfo({
                statusRadio: vm.statusRadio
            });
        },
        confirmItem2: function() {
            let vm = this;
            vm.setHolidayInfo({
                agentname: vm.confirmItem2
            });
        },
        approveType: function() {
            let vm = this;
            vm.setHolidayInfo({
                approveType: vm.approveType
            });
        },
        appLyName: function() {
            let vm = this;
            vm.setHolidayInfo({
                appLyName: vm.appLyName
            });
        }
    },
    methods: {
        ...mapMutations([
            'setHolidayInfo'
        ]),
        getVuex() {
            let vm = this;
            vm.inputNum = vm.holidayInfo.days;
            vm.address = vm.holidayInfo.address;
            vm.remark = vm.holidayInfo.remark;
            vm.checkNum = vm.holidayInfo.checkNum;
            if (vm.checkNum != 99) {
                vm.leaderIDs = vm.holidayInfo.approver[vm.checkNum].operatorid;
            }
            vm.statusRadio = vm.holidayInfo.statusRadio;
            if (vm.statusRadio != 99) {
                vm.confirmItem2 = vm.holidayInfo.agentObj[vm.statusRadio].empname;
                vm.agentId = vm.holidayInfo.agentObj[vm.statusRadio].operatorid;
            }
        },
        // 获取当天日期
        getNowFormatDate() {
            var vm = this;
            if (vm.holidayInfo.endDate == '') {
                var date = new Date();
                var seperator1 = '-';
                var month = date.getMonth() + 1;
                var strDate = date.getDate();
                if (month >= 1 && month <= 9) {
                    month = '0' + month;
                }
                if (strDate >= 0 && strDate <= 9) {
                    strDate = '0' + strDate;
                }
                var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate;
                vm.startdate = currentdate;
                vm.enddate = currentdate;
                vm.outDate = currentdate;
                vm.enterDate = currentdate;
                vm.currentYear = vm.formatDate(currentdate, 'yyyy');
            } else {
                vm.startdate = vm.holidayInfo.startDate;
                vm.enddate = vm.holidayInfo.endDate;
                vm.currentYear = vm.formatDate(vm.startdate, 'yyyy');
            }
        },
        // 页面数据加载
        pageRender() {
            let vm = this;
            if (vm.workId == undefined) {
                vm.$http.all([annualLeave(), getAgent()]).then(vm.$http.spread(function(data1, data2, data3) {
                    if (data2.data.isSuccess == 'true') {
                        vm.$dialog.loading.close();
                    }
                    // 获取年假信息
                    vm.yearObj = data1.data.jsonData;
                    // 获取工作代理人
                    vm.agentObj = data2.data.jsonData;
                    for (let i = 0; i < data2.data.jsonData.length; i++) {
                        let item = data2.data.jsonData[i].empname;
                        vm.agentList.push(item);
                    }
                    // console.log(vm.agentList);
                    var dataApprove = data3.data.jsonData;
                    for (var j = 0; j < dataApprove.length; j++) {
                        var item2 = dataApprove[j];
                        vm.approver.push(item2);
                    }
                }), function(res) {
                    if (res.response.status != 200) {
                        vm.errorTime++;
                        if (vm.errorTime < 3) {
                            vm.pageRender();
                        } else {
                            vm.$dialog.loading.close();
                            // vm.$dialog.toast({
                            //     mes: '表单未加载成功',
                            //     timeout: 2000,
                            //     callback: () => {
                            //         vm.$router.push('/handle');
                            //     }
                            // });
                        }
                    }
                });
            } else if (vm.workId != undefined) {
                vm.$http.all([annualLeave(), getAgent(), getForm(), approveLog()]).then(vm.$http.spread(function(data1, data2, data3, data4) {
                    if (data3.data.isSuccess == 'false') {
                        vm.errorTime++;
                        if (vm.errorTime < 3) {
                            vm.pageRender();
                        } else {
                            vm.$dialog.loading.close();
                            vm.$dialog.toast({
                                mes: '表单未加载成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push('/handle');
                                }
                            });
                        }
                    } else {
                        vm.$dialog.loading.close();
                    }
                    if (data3.data.jsonData.CURRENTSTATE != 10 && data3.data.jsonData.CURRENTSTATE != 4 && vm.$route.params.status == 0 && data3.data.isSuccess == 'true') {
                        vm.$dialog.toast({
                            mes: '该流程已被处理',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function() {
                                    if (vm.$route.params.status == 0) {
                                        vm.$router.push('/handle');
                                    }
                                })
                            }
                        });
                        if (vm.$route.params.status == 0) {
                            return false;
                        }
                    }
                    // 获取年假信息
                    vm.yearObj = data1.data.jsonData;
                    // 获取工作代理人
                    vm.agentObj = data2.data.jsonData;
                    for (let i = 0; i < data2.data.jsonData.length; i++) {
                        let item = data2.data.jsonData[i].empname;
                        vm.agentList.push(item);
                    }
                    // 获取表单数据
                    console.log(data3.data.jsonData);
                    vm.formInfo = data3.data.jsonData;
                    vm.startdate = vm.formInfo.startDate;
                    vm.enddate = vm.formInfo.endDate;
                    vm.inputNum = vm.formInfo.days;
                    vm.confirmItem2 = vm.formInfo.agentname;
                    vm.remark = vm.formInfo.remark;
                    vm.address = vm.formInfo.address;
                    vm.myId = vm.formInfo.applyId;
                    vm.appLyName = vm.formInfo.appLyName;
                    vm.isOut = vm.formInfo.isOut;
                    vm.outDate = vm.formInfo.outDate;
                    vm.enterDate = vm.formInfo.enterDate;
                    vm.isCansend = vm.formInfo.isCansend;
                    for (let keyJ in vm.formInfo.nextBtn) {
                        if (keyJ.indexOf('doApprove') != -1) {
                            for (let key in vm.formInfo.nextBtn.doApprove) {
                                if (key.indexOf('type') != -1) {
                                    vm.approveType = vm.formInfo.nextBtn.doApprove[key];
                                }
                            }
                        } else if (keyJ.indexOf('Activity') != -1) {
                            vm.nextLeaderIDs = vm.formInfo.nextBtn[keyJ][0];
                            vm.nextActivity = keyJ;
                        }
                    }
                    // vm.approveType = vm.formInfo.nextBtn.doApprove.type;
                    // if (vm.approveType == '2') {
                    //     vm.$router.push('/approveHoliday/');
                    // }
                    // 审批流程
                    vm.approvelog = data4.data.jsonData;
                }), function(res) {
                    if (res.response.status != 200) {
                        vm.errorTime++;
                        if (vm.errorTime < 3) {
                            vm.pageRender();
                        } else {
                            vm.$dialog.loading.close();
                            vm.$dialog.toast({
                                mes: '表单未加载成功',
                                timeout: 2000,
                                callback: () => {
                                    vm.$router.push('/handle');
                                }
                            });
                        }
                    }
                });
            }
            // 获取年假信息
            function annualLeave() {
                return vm.$http.get(vm.conf.portalMobile + '/API/AnnLeave/annual/getAnnualLeave', {
                    params: {
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'random': Math.floor(Math.random() * (100000 + 1))
                    }
                });
            }
            // 获取工作代理人
            function getAgent() {
                return vm.$http.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    params: {
                        'type': 'askforleave',
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'queryType': 13,
                        'random': Math.floor(Math.random() * (100000 + 1))
                    }
                });
            }
            // 获取表单数据
            function getForm() {
                return vm.$http.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                    params: {
                        'type': 'askforleave',
                        'workItemID': vm.workId,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'random': Math.floor(Math.random() * (100000 + 1))
                    }
                });
            }
            // 审批流程
            function approveLog() {
                return vm.$http.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryApprove', {
                    params: {
                        'type': 'askforleave',
                        'workItemID': vm.workId,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'random': Math.floor(Math.random() * (100000 + 1))
                    }
                });
            }
        },
        // 获取审批人
        getApprover() {
            var vm = this;
            for (var i = 0; i < vm.queryTypeArr.length; i++) {
                var item = vm.queryTypeArr[i];
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    'type': 'askforleave',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'queryType': item
                }, function(res) {
                    var data = res.jsonData;
                    for (var j = 0; j < data.length; j++) {
                        var item2 = data[j];
                        vm.approver.push(item2);
                    }
                }, function(res) {}, false);
            }
        },
        // 获取公司领导受理人
        getAcceptor() {
            var vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryOAHandlerPerson', {
                'type': 'askforleave',
                'workItemID': 0,
                'activityID': 'acceptActivity',
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'queryType': 21
            }, function(res) {
                let data = res.jsonData;
                vm.approver = data;
                vm.checkNum = 0;
                let leaderIDsArray = [];
                for (let i = 0; i < data.length; i++) {
                    leaderIDsArray.push(data[i].operatorid);
                }
                vm.leaderIDs = leaderIDsArray.join(',');
                console.log(vm.leaderIDs);
            }, function(res) {}, false);
        },
        // 送领导审批
        sendOut() {
            var vm = this;
            var num = (vm.inputNum / 0.5).toString().indexOf('.');
            if (isNaN(vm.inputNum)) {
                vm.$dialog.toast({
                    mes: '时长请输入数字',
                    timeout: 2000
                });
            } else if (vm.inputNum == '' || vm.inputNum < 0.5 || num > 0) {
                vm.$dialog.toast({
                    mes: '请输入请假时长且时长为0.5的倍数',
                    timeout: 2000
                });
            } else if (Math.round(vm.inputNum) > vm.daysNum + 1) {
                vm.$dialog.toast({
                    mes: '输入时长大于实际时长',
                    timeout: 2000
                });
            } else if (vm.remark == '') {
                vm.$dialog.toast({
                    mes: '请输入您的请假事由',
                    timeout: 2000
                });
            } else if (vm.checkNum == 99) {
                vm.$dialog.toast({
                    mes: '请选择审批人',
                    timeout: 1000
                });
            } else {
                if (vm.checkNum != 99) {
                    vm.leaderIDs = vm.approver[vm.checkNum].operatorid;
                }
                if (vm.statusRadio != 99) {
                    vm.agentId = vm.agentObj[vm.statusRadio].operatorid;
                }
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'askforleave',
                    'workItemID': '0',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'activityID': 'applyActivity',
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.userInfo.deptname + vm.userInfo.empname + '请假申请',
                    'ext': '{"startDate":"' + vm.startdate + '","endDate":"' + vm.enddate + '","days":"' + vm.inputNum + '","remark":"' + vm.remark + '","address":"' + vm.address + '",agentId:"' + vm.agentId + '",agentname:"' + vm.confirmItem2 + '"}'
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '提交申请成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/history/true/undefined');
                        }
                    });
                });
            }
        },
        // 送领导审批(总经理室的人)
        sendOutManager() {
            console.log(1);
            var vm = this;
            var num = (vm.inputNum / 0.5).toString().indexOf('.');
            let oDate = new Date(vm.outDate);
            let eDate = new Date(vm.enterDate);
            let sDate = new Date(vm.startdate);
            if (isNaN(vm.inputNum)) {
                vm.$dialog.toast({
                    mes: '时长请输入数字',
                    timeout: 2000
                });
            } else if (vm.inputNum == '' || vm.inputNum < 0.5 || num > 0) {
                vm.$dialog.toast({
                    mes: '请输入请假时长且时长为0.5的倍数',
                    timeout: 2000
                });
            } else if (Math.round(vm.inputNum) > vm.daysNum + 1) {
                vm.$dialog.toast({
                    mes: '输入时长大于实际时长',
                    timeout: 2000
                });
            } else if (vm.remark == '') {
                vm.$dialog.toast({
                    mes: '请输入您的请假事由',
                    timeout: 2000
                });
            } else if (vm.address == '') {
                vm.$dialog.toast({
                    mes: '请输入您的请假地点',
                    timeout: 2000
                });
            } else if (vm.checkNum == 99) {
                vm.$dialog.toast({
                    mes: '请选择审批人',
                    timeout: 1000
                });
            } else if ((oDate.getTime() > eDate.getTime()) && vm.isOut == 1) {
                vm.$dialog.toast({
                    mes: '入境时间需大于离境时间',
                    timeout: 2000
                });
            } else if ((oDate.getTime() < sDate.getTime()) && vm.isOut == 1) {
                vm.$dialog.toast({
                    mes: '离境时间需大于休假开始时间',
                    timeout: 2000
                });
            } else {
                if (vm.checkNum != 99) {
                    vm.leaderIDs = vm.approver[vm.checkNum].operatorid;
                }
                if (vm.statusRadio != 99) {
                    vm.agentId = vm.agentObj[vm.statusRadio].operatorid;
                }
                if (vm.isOut == 0) {
                    vm.outDate = '';
                    vm.enterDate = '';
                }
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'askforleave',
                    'workItemID': '0',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'activityID': 'applyActivity',
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.userInfo.deptname + vm.userInfo.empname + '请假申请',
                    'ext': '{"startDate":"' + vm.startdate + '","endDate":"' + vm.enddate + '","days":"' + vm.inputNum + '","remark":"' + vm.remark + '","address":"' + vm.address + '",agentId:"' + vm.agentId + '",agentname:"' + vm.confirmItem2 + '",isOut:"' + vm.isOut + '",outDate:"' + vm.outDate + '",enterDate:"' + vm.enterDate + '"}'
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '提交申请成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/history/true/undefined');
                        }
                    });
                });
            }
        },
        // 送受理人(公司领导)
        sendAccepted() {
            var vm = this;
            var num = (vm.inputNum / 0.5).toString().indexOf('.');
            let oDate = new Date(vm.outDate);
            let eDate = new Date(vm.enterDate);
            let sDate = new Date(vm.startdate);
            if (isNaN(vm.inputNum)) {
                vm.$dialog.toast({
                    mes: '时长请输入数字',
                    timeout: 2000
                });
            } else if (vm.inputNum == '' || vm.inputNum < 0.5 || num > 0) {
                vm.$dialog.toast({
                    mes: '请输入请假时长且时长为0.5的倍数',
                    timeout: 2000
                });
            } else if (Math.round(vm.inputNum) > vm.daysNum + 1) {
                vm.$dialog.toast({
                    mes: '输入时长大于实际时长',
                    timeout: 2000
                });
            } else if (vm.address == '') {
                vm.$dialog.toast({
                    mes: '请输入您的请假地点',
                    timeout: 2000
                });
            } else if (vm.remark == '') {
                vm.$dialog.toast({
                    mes: '请输入您的请假事由',
                    timeout: 2000
                });
            } else if ((oDate.getTime() > eDate.getTime()) && vm.isOut == 1) {
                vm.$dialog.toast({
                    mes: '入境时间需大于离境时间',
                    timeout: 2000
                });
            } else if ((oDate.getTime() < sDate.getTime()) && vm.isOut == 1) {
                vm.$dialog.toast({
                    mes: '离境时间需大于休假开始时间',
                    timeout: 2000
                });
            } else {
                // if (vm.checkNum != 99) {
                //     vm.leaderIDs = vm.approver[vm.checkNum].operatorid;
                // }
                if (vm.isOut == 0) {
                    vm.outDate = '';
                    vm.enterDate = '';
                }
                if (vm.statusRadio != 99) {
                    vm.agentId = vm.agentObj[vm.statusRadio].operatorid;
                }
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'askforleave',
                    'workItemID': '0',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'activityID': 'acceptActivity',
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.userInfo.deptname + vm.userInfo.empname + '请假申请',
                    'ext': '{"startDate":"' + vm.startdate + '","endDate":"' + vm.enddate + '","days":"' + vm.inputNum + '","remark":"' + vm.remark + '","address":"' + vm.address + '","agentId":"' + vm.agentId + '",agentname:"' + vm.confirmItem2 + '",isOut:"' + vm.isOut + '",outDate:"' + vm.outDate + '",enterDate:"' + vm.enterDate + '"}'
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '提交申请成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/history/true/undefined');
                        }
                    });
                });
            }
        },
        // 提交按钮（销假）
        cancelHoliday() {
            var vm = this;
            if (vm.nextActivity == 'logoutActivity') {
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'askforleave',
                    'workItemID': vm.workId,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'activityID': 'logoutActivity',
                    'leaderIDs': vm.myId,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.formInfo.firstorgname + vm.formInfo.appLyName + '请假申请',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '提交申请成功<br>您的请假单已正式提交',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'askforleave',
                    'workItemID': vm.workId,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'activityID': vm.nextActivity,
                    'leaderIDs': vm.nextLeaderIDs,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.formInfo.firstorgname + vm.formInfo.appLyName + '请假申请',
                    'ext': '{"startDate":"' + vm.startdate + '","endDate":"' + vm.enddate + '","days":"' + vm.inputNum + '","remark":"' + vm.transferString(vm.remark) + '","address":"' + vm.address + '",agentId:"' + vm.agentId + '",agentname:"' + vm.confirmItem2 + '"}'
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '提交申请成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            }
        },
        // flag显示
        toSelectLeader() {
            let vm = this;
            vm.$router.push('/selectLeader/true/' + vm.$route.params.workId);
        },
        // 跳转到历史记录
        toHistory() {
            let vm = this;
            if (vm.workId == 'undefined') {
                vm.$router.push('/history/true/undefined');
            } else {
                vm.$router.push('/history/true/' + vm.workId);
            }
        }
    }
}
</script>
